import { qiankunStart } from 'umi';
import request from '../services/request';

export const qiankun = request('/apps').then(apps => ({apps
}));

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

export default {
  namespace: 'base',

  state: {
    name: 'Qiankun',
    apps: [],
  },

  effects: {
    *getApps(_, { put }) {
      /*
       
       1app.jsqiankun2umiimport @tmp/subAppsConfig
      */
      console.log('waiting for qiankun start');
      var apps = [];
      yield  request('/apps').then(res=>{
        apps = res
      })
      yield sleep(1000);
      yield put({
        type: 'getAppsSuccess',
        payload: {
          apps,
        },
      });

      //  qiankun ,  defer  true
      setTimeout(qiankunStart, 200);
      // qiankunStart()
    },
  },

  reducers: {
    getAppsSuccess(state, { payload }) {
      return {
        ...state,
        apps: payload.apps,
      };
    },
  },
};
